import React from 'react';
import {genPromise} from '../plugins';
import 'styled-components';
import {News} from 'styled-icons/boxicons-regular/News';
import {FilePdf} from 'styled-icons/icomoon/FilePdf';
import {Home6} from 'styled-icons/remix-line/Home6';
import {Project as ProjectIcon} from 'styled-icons/octicons/Project';
import ResultView from './result.jsx';

class PaperItem extends React.Component{
    render(){
        return(
            <div className='card'>
                <div className='card-header'>{this.props.conf?this.props.conf:"期刊论文"}</div>
                <div className='card-body'>
                    <h6 className='card-title'>{this.props.title}</h6>
                   
                    <p className='card-text'>{this.props.author}</p>
                    {this.props.link&&<a className='card-link btn btn-self' href={this.props.link}>
                        <FilePdf className='mr-1' size='15'/>
                        Paper</a>}
                </div>
            </div>
        )
    }
}

class Paper extends React.Component{
    constructor(props){
        super(props);
        this.state={
            paper_list:[],
            error:false,
            error_msg:'error',
            load:false
        }
    }

    componentDidMount(){
        genPromise('GET','http://39.100.113.162/info/paper/paper.php',{
            limit:6
        }).then(
            (data)=>{
                if(data.code===0){
                    this.setState({
                        paper_list:data.data,
                        error:false,
                        load:true
                    })
                }else{
                    this.setState({
                        error:true
                    })
                }
            },(error)=>{
                console.log(error);
               
                this.setState({
                    error:true,
                    error_msg:error.message
                })
            }
        )
    }

    render(){

   
        const paper_list = this.state.paper_list.slice(0);
        var new_paper = [],
            count = 0,
            limit = 6;
        paper_list.sort((a, b) => {
            return b.year - a.year;
        })
        paper_list.forEach((el) => {
            el.paper_list.forEach((sel) => {
                if (count < limit) {
                    new_paper.push(
                        <PaperItem key={sel.paper_id}
                            link={sel.pdf_link}
                            author={sel.author}
                            conf={sel.conf}
                            title={sel.paper_title} />
                    )
                }
                count++;
            })
        })
        const ContentLoader=()=>(
            <div className='content-loader d-flex justify-content-between'>
                <div className='w-100 p-2'>
                <div className='content-loader-rect rounded-pill  m-3'
                    style={{ width: '85%', height: '.8rem' }}>
                        <div className="animated-bg"></div>
                    </div>
                    <div className='content-loader-rect rounded-pill  m-3'
                    style={{ width: '60%', height: '.8rem' }}>
                        <div className="animated-bg"></div>
                    </div>
                    <div className='content-loader-rect rounded-pill  m-3'
                    style={{ width: '60%', height: '.8rem' }}>
                        <div className="animated-bg"></div>
                    </div>
                    
                </div>
                <div className='w-50 p-2'>
                <div className='content-loader-rect rounded-pill  m-3'
                    style={{ width: '60%', height: '.8rem' }}>
                        <div className="animated-bg"></div>
                    </div>
                    <div className='content-loader-rect rounded-pill  m-3'
                    style={{ width: '75%', height: '.8rem' }}>
                        <div className="animated-bg"></div>
                    </div>
                    
                </div>
            </div>);
        return(
            <div className='paper-container col-sm-8 pr-md-4'>
                <h4>
                    <News className='align-bottom' size='30'/>
                    最新论文
                </h4>
                {
                    this.state.load?
                    (this.state.error?
                    <ResultView text={this.state.error_msg} />:
                    (<div className='card-group'>
                        {new_paper}
                    </div>)
                    ):
                    <React.Fragment>
                        <ContentLoader />
                        <ContentLoader />
                    </React.Fragment>
                }
            
            </div>
        )
    }

}

class ProjectItem extends React.Component{
    render(){
        try{
            const title = this.props.title;
            const mm = this.props.title.match(/[\n\r]+/);
            const index = mm['index'];
            const name_mm=title.slice(index).match(/([\u4e00-\u9fa5]+)/);
            var name=name_mm[0],
                en_title=title.slice(index+name_mm['index']+name.length);
            return (
                <li>
                    <div >
                        <p >
                            {title.slice(0, index)}
                           <span>{en_title}</span>
                        </p>
                        
                        <p>
                            {name} <span>{this.props.time}</span>
                        </p>
                    </div>
                </li>
            )
        }
        catch(error){
            return(
                <li>
                    <div>
                        <p>{this.props.title}</p>
                    </div>
                </li>
            )
        }
    }
}

class Project extends React.Component{
    constructor(props){
        super(props)
        this.state={
            project_list:[],
            error:false,
            error_msg:'',
            load:false
        }
    }

    componentDidMount(){
        const time_test = /((\d{4})年(\d{1,2}月)?[-至](\d{4})年(\d{1,2}月)?)|((\d{4})\.\d{1,2}\.\d{1,2}[-至](\d{4})\.\d{1,2}\.\d{1,2})[\n\r]*$/;
        let project_list=[];
        genPromise('GET', 'http://39.100.113.162/info/project/project.php').then(
            (data) => {
                if (data.code === 0) {
                    data.data.forEach((el) => {
                        try{
                            var project_time = el.project_name.match(time_test);
                            console.log(project_time);
                        
                            //el.time = project_time[2] || project_time[7] || new Date().getFullYear();
                            el.project_name = el.project_name.slice(0, project_time['index']);
                            el.project_time = project_time[0];
                            if(parseInt(project_time[4])>=new Date().getFullYear()){
                                project_list.push(el);
                            }
                        }catch(error){
                            console.log(error);
                        }
                    })
                   
                    //console.log(data.data);
                    this.setState({
                        project_list: project_list,
                        load: true
                    })
                } else {
                    this.setState({
                        error: true
                    })
                }
            }, (error) => {
                console.log(error);
                this.setState({
                    error: true,
                    error_msg: error.message
                })
            }
        )
    }
    render(){
        const project_list=this.state.project_list.map((el)=>
            <ProjectItem key={el.project_id} title={el.project_name} time={el.project_time} />
        );
        const ContentLoader=()=>(
            <div className='content-loader d-flex justify-content-between'>
                <div className='w-100 p-2'>
                <div className='content-loader-rect rounded-pill  m-3'
                    style={{ width: '85%', height: '.8rem' }}>
                        <div className="animated-bg"></div>
                    </div>
                    <div className='content-loader-rect rounded-pill  m-3'
                    style={{ width: '60%', height: '.8rem' }}>
                        <div className="animated-bg"></div>
                    </div>
                    <div className='content-loader-rect rounded-pill  m-3'
                    style={{ width: '60%', height: '.8rem' }}>
                        <div className="animated-bg"></div>
                    </div>
                    
                </div>
                <div className='w-50 p-2'>
                <div className='content-loader-rect rounded-pill  m-3'
                    style={{ width: '85%', height: '.8rem' }}>
                        <div className="animated-bg"></div>
                    </div>
                    <div className='content-loader-rect rounded-pill  m-3'
                    style={{ width: '75%', height: '.8rem' }}>
                        <div className="animated-bg"></div>
                    </div>
                    
                </div>
            </div>);
        return(
            <div className='project-container'>
                <h4>
                <ProjectIcon className='align-bottom' size='30 '/>最近项目</h4>
                {
                    this.state.load?
                    (this.state.error?
                    <ResultView text={this.state.error_msg} />:
                    (<ul className='card-group'>
                        {project_list}
                    </ul>)
                    ):
                    <React.Fragment>
                        <ContentLoader />
                        <ContentLoader />
                    </React.Fragment>
                }
            </div>
        )
    }
}

