import React from 'react';
import Header from './header'
import Home from './home';
import $ from 'jquery';
class ShowImg extends React.Component{
    constructor(props){
        super(props);
        this.state={
            imgLoad:false
        }
        this.preload=this.preload.bind(this);
        //this.addLoadEvent(this.preload);
        $(document).ready(this.preload);
    }
    preload(){
        var imgList=[
            './img/general1.jpg',
            './img/whu_1_1.jpg',
            './img/whu_3_3.jpg'
        ],
        count=imgList.length,
        k=0,
        images=new Array();
        for(let i=0;i<count;i++){
            images[i]=new Image();
            images[i].src=imgList[i];
            images[i].onload=()=>{
                k++;
                if(k===count){
                    this.setState({
                        imgLoad:true
                    })
                }
            }
        }
    }
    addLoadEvent(func) {  
        var oldonload = window.onload;  
        if (typeof window.onload != 'function') {  
            window.onload = func;  
        } else {  
            window.onload = function() {  
                if (oldonload) {  
                    oldonload();  
                }  
                func();  
            }  
        }  
    }  
    render(){
        return(
           
           
            <div id="carouselIndicators" className="carousel slide carousel-fade w-100" data-ride="carousel">
                
                <div  className="carousel-inner" >
                    {/*<div  className="carousel-item active">
                        <img src="../img/general1.png" className="d-block w-100 h-100" alt="..."/>
                    </div>
                    <div  className="carousel-item">
                        <img src="../img/general2.png" className="d-block w-100 h-100" alt="..."/>
                    </div>
                    <div className="carousel-item">
                        <img src="../img/competition4.png" className="d-block w-100 h-100" alt="..."/>
                    </div>
                    */}
                    <div className='carousel-item active'
                         style={{backgroundImage:
                         this.state.imgLoad?'url(./img/general1.jpg)':'url(./img/general1_m.jpg'}}>

                    </div>
                    <div className='carousel-item'
                         style={{backgroundImage:
                         this.state.imgLoad?'url(./img/whu_1_1.jpg)':'url(./img/whu_1_1_m.jpg'}}>

                    </div>
                    <div className='carousel-item'
                         style={{backgroundImage:
                         this.state.imgLoad?'url(./img/whu_3_3.jpg)':'url(./img/whu_3_3_m.jpg'}}>

                    </div>
                </div>
                <a className="carousel-control-prev" href="#carouselIndicators" role="button" data-slide="prev">
                    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
                    <span className="sr-only">Previous</span>
                </a>
                <a className="carousel-control-next" href="#carouselIndicators" role="button" data-slide="next">
                    <span className="carousel-control-next-icon" aria-hidden="true"></span>
                    <span className="sr-only">Next</span>
                </a>
            </div>
            
        )
    }
}

